//**functions
const fade = (element, effect, delay, time) =>{
    let delayElement = delay;
    for(let i = 0; i < element.length; i++ ){
        delayElement += 0.05; 
        let delayResult = delayElement.toFixed(2);
        element[i].style.animation = `${effect} .5s cubic-bezier(.694, .048, .335, 1)`;
        element[i].style.setProperty("animation-delay", `${delayResult}s`);
        setTimeout(()=>{ element[i].style.opacity = 1},time)
    }
}

const toTop = (element) =>{
    let scrollPosition = element.getBoundingClientRect().top;
    let screenSize = window.innerHeight;
  
    if(scrollPosition < screenSize - 100){
        element.style.animation = "toUpUp 1s none";
        element.style.opacity = 1;
    }
    // else{
    //     element.style.transition = "all .5s ease";
    //     element.style.opacity = 0;
    //     element.style.animation = "";
    // }
}

// efecto con varios elementos de una seccion
const toTop2 = (element) =>{
    let element1 = element[0];
    let scrollPosition = element1.getBoundingClientRect().top;
    let screenSize = window.innerHeight;
  
    if(scrollPosition < screenSize - 100){
        let delayElement = 0;
        for(let i = 0; i < element.length; i++ ){
            delayElement += 0.05; 
            let delayResult = delayElement.toFixed(2);
            element[i].style.animation = "toUpUp 1s none";
            element[i].style.setProperty("animation-delay", `${delayResult}s`);
            setTimeout(()=>{ element[i].style.opacity = 1},500)
        }
    }
    
}

//**MENU responsive
function menu() {
    const mobileMenu = document.querySelector(".mobile-menu");
    mobileMenu.addEventListener("click", navegacionResponsive);
}

function cerrarMenu(){
    const navegacion = document.querySelector(".navegacion-principal");
    const main = document.querySelector("main");
    if (navegacion.classList.contains("mostrar")) {
        navegacion.classList.remove("mostrar");
        main.classList.remove("blur-child");
        document.body.classList.remove("blur");
    }
}

function navegacionResponsive() {
    // mostrar menu
    const navegacion = document.querySelector(".navegacion-principal");
    navegacion.classList.toggle("mostrar");
    const main = document.querySelector("main");
    main.classList.toggle("blur-child");
    document.body.classList.toggle("blur");

    // cerrar menu
    const mainContent = document.querySelector("#main-content");
    mainContent.addEventListener("click", cerrarMenu);
    addEventListener("scroll", cerrarMenu);
}



